$(document).ready(function () {
    $(".nav__link").on('click', function (event) {
        if (this.hash !== "") {
            event.preventDefault();
            var hash = this.hash;
            $('html, body').animate({
                scrollTop: $(hash).offset().top
            }, 300, function () {
                window.location.hash = hash;
            });
        }
    });
    setTimeout(function () {
        window.scrollTo(0, 1)
    }, 100);
});